import { VariantAProps } from "@/types";

export function VariantA({ humidity, windSpeed, feelsLike }: VariantAProps) {
  return (
    <div className="flex flex-wrap justify-center gap-4 mt-4">
          <div className="p-4 bg-gray-700 rounded-lg text-center flex-1 min-w-[150px] max-w-[200px]">
            <p className="text-sm">Humidity</p>
            <p className="text-xl font-bold">{humidity}%</p>
          </div>
          <div className="p-4 bg-gray-700 rounded-lg text-center flex-1 min-w-[150px] max-w-[200px]">
            <p className="text-sm">Wind</p>
            <p className="text-xl font-bold">{windSpeed} m/s</p>
          </div>
          <div className="p-4 bg-gray-700 rounded-lg text-center flex-1 min-w-[150px] max-w-[200px]">
            <p className="text-sm">Feels like</p>
            <p className="text-xl font-bold">{Math.round(feelsLike)}°</p>
          </div>
        </div>
  );
}
